.App {
  text-align: center;
  font-family: fantasy;
}
.App-header {
  background-color: #101010;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.primeira {
  display: flex;
  background-color: #202020;
  border-radius: 2px;
  margin: 2%;
  width: 90%;
  margin-top: 0;
}
.img-user {
  display: flex;
  flex-direction: row;
  border-radius: 50%;
  height: 150px;
  width: 150px;
  padding: 10px;
}
.img-user1 {
  border-radius: 2px;
  height: 300px;
  width: 200px;
}

.segunda {
  display: flex;
  background-color: #202020;
  border-radius: 2px;
  margin: 2%;
  width: 90%;
}

.terceira {
  display: flex;
  background-color: #202020;
  border-radius: 2px;
  margin: 2%;
  width: 90%;
}

.quarta {
  display: flex;
  background-color: #202020;
  border-radius: 2px;
  margin: 2%;
  width: 90%;
}

.quinta {
  display: flex;
  background-color: #202020;
  border-radius: 2px;
  margin: 2%;
  width: 90%;
  margin-bottom: 0;
  justify-content: center;
}
.social {
  background-color: transparent;
  margin: 10px;
  padding: 4px;
  border: 3px #fff;
  color: #01cafe;
  cursor: pointer;
  outline: 0 !important;
}
p {
  margin-left: 2%;
  margin-right: 2%;
  font-size: large;
  text-align: start;
}
.title {
  text-align: start;
  margin-left: 2%;
  color: #01cafe;
}
